import importFrom from "import-from";
import type { GatsbyCache } from "../../../types";

export function getCache(): GatsbyCache {
  if (global._polyfillRemoteFileCache) {
    return global._polyfillRemoteFileCache;
  }
  // We need to use the project's version of @netlify/content-engine to pick up the correct path to
  // the cache
  const { getCache: getContentEngineCache } = importFrom(
    global.__GATSBY?.root ?? process.cwd(),
    `@netlify/content-engine/dist/utils/get-cache`,
  ) as { getCache: (key: string) => GatsbyCache };

  const cache = getContentEngineCache(`content-engine`);
  global._polyfillRemoteFileCache = cache;
  return cache;
}
